import React from 'react';
import styled from 'styled-components';

const IndexComponentBlock = styled.div`
`;

const MainImageWrapper = styled.img`
  width: 100%;
`;

const AdditionalInfo = styled.img`
  width: 100%;
`;

const IndexComponent = () => {
  return (
    <IndexComponentBlock className="container my-3" style={{minHeight:"600px"}}>
      <div className="jumbotron jumbotron-fluid my-5">
        <MainImageWrapper src="/images/index/vis_1.jpg" alt="대문" />
      </div>
      <div className="jumbotron jumbotron-fluid my-5 col-lg-6 col-md-9 mx-auto">
        <AdditionalInfo src="/images/index/doorisimportant.png" alt="" />
      </div>
    </IndexComponentBlock>
  );
};

export default IndexComponent;
